.about-us {
  min-height: 1066px;
  box-shadow: 0 4px 20px 0 $shadow-color;

  &__description {
    margin: 0 auto 65px;
    font-size: 18px;
    line-height: 30px;
    text-align: center;
  }

  &__description-title {
    margin: 0 0 30px;
    font-weight: 700;
  }

  &__description-title > span {
    text-decoration: underline;
  }

  &__cards-block {
    display: flex;
    justify-content: space-between;
    text-align: center;
  }

  &__card {
    max-width: 269px;
    min-height: 100%;
    padding: 0 15px 30px;
    margin-bottom: 65px;
    border-radius: 20px;
    box-shadow: 0 4px 20px 0 $shadow-color;
    
    transition: 300ms;

    &:hover {
      transform: scale(1.1);
    }
  }

  &__card > span {
    font-family: "Poppins", sans-serif;
    font-size: 16px;
    line-height: 23px;

    color: $black;
  }

  &__logo-box {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    max-width: 230px;
    min-height: 237px;
    margin: -90px auto 0;
    padding: 53px;
    transform: translateY(90px);
    border-radius: 50%;
    background-color: $white;
    box-shadow: 0 15px 20px -10px $shadow-color;
  }

  &__logo {
    width: 62px;

  }
}
