.header {
  min-height: 1080px;
  background-image: url("/images/main_picture.jpg");
  background-size: cover;
  background-repeat: no-repeat;

  &__menu-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px;
    max-width: 1920px;
    margin: 0 auto;
  }

  &__menu {
    padding: 26px 50px;
    background-color: $white;
    box-shadow: 0 5px 20px 0 #b1b1b166;
  }

  &__menu-logo {
    width: 179px;
    height: 53px;
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 975px;
  }

  &__main-picture {
    max-width: 626px;
    max-height: 296px;
  }

  &__content-logo {
    margin: 0 auto 53px;
    max-width: 132px;
  }
}
