.nav {
 
  &__list {
    display: flex;
    align-items: center;
    text-align: center;
    gap: 40px;
    margin: 0;
    padding: 0;
    list-style: none;
  }

  &__link {
    display: block;
    padding: 14px 0;
    font-size: 16px;
    line-height: 24px;
    text-decoration: none;
    color: #000;
    white-space: nowrap;
    transition: 300ms;

    &:hover {
      color: $main-blue-color;
      text-decoration: underline;
    }
  }
}

.active {
  color: $main-blue-color;
  text-decoration: underline;
}
