.opinions {
  background-color: $light-grey;
  margin-top: 14px;
  padding: 180px 0 94px;

  &__slider-box {
    position: relative;
    padding: 130px;
    background-color: $main-blue-color;
    border-radius: 10px;

    &::before {
      position: absolute;
      content: url("/images/signs1.svg");
      top: -26px;
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__slider-buttons {
    position: absolute;
    display: flex;
    gap: 22px;
    width: 252px;
    height: 30px;
    bottom: 35px;
    left: 50%;
    transform: translateX(-50%);
  }

  &__slider-button {

    transition: 300ms;

    &:hover {

      transform: scale(1.5);
      cursor: pointer;
    }
  }

  &__process {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  &__opinion-box {
    display: flex;
    overflow: hidden;
    border-radius: 20px;
    box-shadow: 0 0 20px 0 $blue-color;
  }

  &__opinion {
    min-width: 100%;
    padding: 40px 97px;
    text-align: center;
    background-color: $white;
  }

  &__opinion > h5 {
    text-transform: uppercase;
    color: $blue-color;
  }

  &__opinion > span {
    font-size: 20px;
    line-height: 32px;
  }

  &__opinion > div {
    margin: 15px 0 29px;
  }
}
